.container {
  height: calc(100vh - 60px);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5rem;
  margin-top: 3.5rem;
}

.wrapper {
  display: flex;
  flex-direction: column;
  border: 1px solid #444;
  border-radius: 22px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.7);
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.title {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 32px;
  font-weight: bold;
}

.inputWrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 0.5rem;
}

.inputWrapper > label {
  margin-right: 15px;
  width: 33%;
}

.inputWrapperImage {
  width: 400px;
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.inputWrapperImage span {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: teal;
  color: #fff;
  border: 1px solid transparent;
  cursor: pointer;
  transition: 150ms all;
  border-radius: 16px;
  margin: 0.1rem 4.75rem;
}

.inputWrapperImage span:hover {
  background-color: #fff;
  color: teal;
  border-color: teal;
}

.imageName {
  position: absolute;
  right: 35rem;
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
}

.closeIcon {
  cursor: pointer;
  font-size: 16px;
}

.input {
  padding: 0.5rem;
  width: 77.5%;
  border-radius: 8px;
  border: 1px solid rgb(170, 165, 165);
}

.input:focus {
  outline: none;
  border-color: rgb(99, 92, 92);
}

.buttonWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 6px;
}
.returns {
  text-decoration: none;
  color: #191919;
  /* margin-bottom: -50px; */
  margin-left: 520px;
  font-weight: 500;
}
.submitBtn {
  width: 50%;
  background-color: #cea600;
  color: #fff;
  padding: 0.75rem 1.25rem;
  border-radius: 12px;
  border: 1px solid transparent;
  cursor: pointer;
  transition: 150ms all;
  margin-top: 1rem;
}

.submitBtn:hover {
  background-color: #fff;
  color: #cea600;
  border-color: #cea600;
}
